@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}
.main_contact{
  width: 100%;
  height: 100vh; 
  background-image: url(/src/Images/Tech_Image2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
.contact_page{
  display: flex;
  margin-right: 200px;
  justify-content: flex-end;
}
.mainContact{
  width: 400px;
  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact_heading{
  margin: 50px 0px 50px 0px;
}
.mainForm{
  /* border: 2px solid grey; */
  background-color: #012642;
  max-width: 360px;
  height: 54vh;
  max-height: 80vh;
  border-radius: 20px;
  padding: 10px;
}
.contact_heading p {
  font-size: 40px;  
  font-weight: 700;
  color: yellow;
}
input, textarea{
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
  width: 340px;
  /* height: 30vh; */
  background-color:#012642;
  color:white;
  margin: 10px 0px 10px 0px;
  padding: 5px 0px 5px 0px;
}
textarea{
  min-width: 340px;
  max-width: 340px;
  max-height: 20vh;
  min-height: 25px;
}
.no_outline:focus {
  outline: none;
  background-color:#032c4c;
}
.submit_btn button{
  background-color: rgb(6, 255, 168);
  padding: 6px;
  width: 70px;
  align-self: baseline;
  margin-top: 10px;
  border-radius: 15px;
  font-size: 15px;
  border: none;
  font-weight: 600;
}
.submit_btn button:hover{
  background-color: rgb(98, 233, 139);
  /* color: #f9f9f9; */
}


@media screen and (max-width:500px) {
  .contact_page{
    display: flex;
    margin-right: 0 ;
    justify-content: center;
  }
} 
input:invalid + span::after {
  color: white;
  content: "Age cannot be less than 18 year";
}
tbody tr td,thead tr td{
  padding: 10px;
  margin: 10px; 
}